<template>
  <div class="app-container">
    <div class="head_nav">
      <van-search class="head_search" v-model="search" placeholder="请输入搜索关键词">
        <template #left-icon>
          <van-icon class-prefix="iconfont iconsousuo" name="iconsousuo" />
        </template>
      </van-search>
    </div>
    <div class="content">
      <div class="history">
        <div class="title">
          <span class="left">历史搜索</span>
          <span class="right">
            <van-icon name="delete" size="22px" color="#999999" />
          </span>
        </div>
        <div class="tag">
          <span>中医药</span>
          <span>养生饮食</span>
          <span>运动健身</span>
          <span>穴位针灸</span>
          <span>保健药材</span>
          <span>知识百科</span>
        </div>
      </div>
      <div class="hot">
        <div class="title">
          <span class="left">热门搜索</span>
          <span class="right"></span>
        </div>
        <div class="tag">
          <span>中医药</span>
          <span>养生饮食</span>
          <span>运动健身</span>
          <span>穴位针灸</span>
          <span>保健药材</span>
          <span>知识百科</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      search: "",
    };
  },
  watch: {},
  computed: {},
  methods: {},
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.app-container {
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  .head_nav {
    position: relative;
    &::after {
      position: absolute;
      box-sizing: border-box;
      content: " ";
      pointer-events: none;
      top: -50%;
      right: -50%;
      bottom: -50%;
      left: -50%;
      border-bottom: 1px solid #ebedf0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
    .head_search {
      width: 100%;
      padding: 10px 18px;
      .van-search__content {
        border: solid 1px #7e3330;
        border-radius: 20px;
        background-color: #fff;
        /deep/ .van-cell {
          font-size: 14px;
          padding: 5px 8px 5px 0;
          .van-field__left-icon {
            height: 100%;
            i {
              color: #7e3330;
              display: flex;
              box-sizing: border-box;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
  .content {
    padding: 10px 18px;
    .history,.hot {
      width: 100%;
      .title {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        font-size: 16px;
        .left {
          color: #333333;
        }
      }
      .tag {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        padding: 5px 0 20px;
        span {
          font-size: 14px;
          padding: 5px 12px;
          color: #666666;
          background: #f5f5f5;
          border-radius: 27px;
          margin-left: 10px;
          margin-bottom: 10px;
          &:first-child {
            margin-left: 0;
          }
        }
      }
    }
  }
}
</style>


